/* Swiper.js */
.swiper-pagination {
  top: 90% !important;
}

.swiper-button-prev {
  top: 92.3% !important;
  left: 40% !important;
}
.swiper-button-prev::after {
  display: none;
}
@media (max-width: 768px) {
  .swiper-button-prev {
    display: none !important;
  }
}

.swiper-button-next {
  top: 92.3% !important;
  right: 40% !important;
}
.swiper-button-next::after {
  display: none;
}
@media (max-width: 768px) {
  .swiper-button-next {
    display: none !important;
  }
}

.swiper-pagination-bullet-active {
  background: #039664 !important;
}

/* Breadcrumbs */
.breadcrumbs {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 5;
}
.breadcrumbs__link {
  font-size: 1.2rem;
  font-weight: 400;
}
.breadcrumbs__previous {
