/* TECH OVERVIEW FORM */
.popup-form-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  display: none;
  align-items: center;
  justify-content: center;
  -webkit-backdrop-filter: blur(10px) brightness(0.5);
          backdrop-filter: blur(10px) brightness(0.5);
