/* Dropdown Menu */
.header-dropdown-target {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.3s;
}
.header-dropdown-target > .dropdown-target-arrow {
  margin-left: 0.6rem;
  display: flex;
  align-items: center;
  transition: all 0.4s;
}
.header-dropdown-target:hover {
  color: #FFFFFF !important;
}
.header-dropdown-target.active .dropdown-target-arrow {
  transform: rotate(180deg);
}

.h-dropdown {
  position: absolute;
  top: 225%;
  cursor: default;
  background: #FFFFFF;
  border-radius: 12px;
  padding: 1.6rem;
  display: flex;
  flex-direction: column;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s;
}
.h-dropdown.active {
  visibility: visible;
  opacity: 1;
}
.h-dropdown.active::before {
  opacity: 1;
}
.h-dropdown::before {
  content: "";
  position: absolute;
  width: 22px;
  height: 22px;
  background: #FFFFFF;
  transform: rotate(45deg);
  top: -11px;
  left: calc(50% - 11px);
  transition: all 0.3s;
  opacity: 0;
}
.h-dropdown__banner {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem;
  background: #EBEBEB;
  border-radius: 6px;
}
.h-dropdown__banner:hover {
  background: #B4DED0;
}
.h-dropdown__banner-title {
  margin: 0 1.2rem;
  width: -moz-max-content;
  width: max-content;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.28;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #333447;
}
.h-dropdown__links {
  margin-top: 1.6rem;
  opacity: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: all 0.3s;
}
.h-dropdown.active .h-dropdown__links {
  opacity: 1;
}
.h-dropdown__link {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.28;
  text-transform: capitalize;
  color: #333447 !important;
  transition: all 0.3s;
}
.h-dropdown__link:not(:last-child) {
  margin-bottom: 1.2rem;
}
.h-dropdown__link:hover {
  color: #757575 !important;
}

